import React from 'react'
import { Form } from 'antd'
import Input from '@/components/input'
import { Modal, ModalProps } from '@/components/report-template-modal'
import TemplateWrapper from '@/components/report-template-wrapper'
import useVisible from '@/hooks/useVisible'
import { createFormItemLayout } from '@/utils/form'
import { ReportTemplateSetting } from '@/constants/types'

type Props = {}

interface Setting extends ReportTemplateSetting {}

export default function TemplateSectionHeader(props: Props) {
  const [setting, setSetting] = React.useState<Setting>({})

  const [editModalVisible, handleEditModalVisible] = useVisible()

  const handleOk = (values: Setting) => {
    setSetting(values)
  }

  return (
    <>
      {editModalVisible && (
        <InputTextModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
        />
      )}
      <TemplateWrapper
        titleVisible
        bodyVisible={false}
        title={setting.text}
        onEdit={handleEditModalVisible}></TemplateWrapper>
    </>
  )
}

function InputTextModal({
  onOk,
  onCancel,
  item,
  ...props
}: ModalProps<Setting>) {
  const [form] = Form.useForm()

  const handleOk = () => {
    form.validateFields().then(values => {
      if (onOk) {
        onOk(values)
      }
      onCancel()
    })
  }

  return (
    <Modal<Setting>
      {...props}
      title={
        <>
          <div>
            Section Header
            <div className="inline ml-2 text-sm text-gray-600">
              typically used for section.
            </div>
          </div>
        </>
      }
      onCancel={onCancel}
      onOk={handleOk}>
      <Form
        {...createFormItemLayout(5, 16)}
        labelAlign="left"
        form={form}
        initialValues={item}>
        <Form.Item label="Text" name="text">
          <Input />
        </Form.Item>
        <Form.Item label="Tooltip" name="tooltip">
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}
