import React from 'react'
import { Modal } from 'antd'
import { ModalProps, ModalFuncProps } from 'antd/lib/modal'

export const modalDefaultProps = (
  content?: React.ReactNode,
  props?: ModalProps
): ModalFuncProps => ({
  content: (
    <div
      className="text-primary font-medium"
      css={{ marginTop: 16, marginBottom: 30 }}>
      {content}
    </div>
  ),
  icon: null,
  width: 440,
  okText: '刪除',
  cancelText: '取消',
  centered: true,
  // maskStyle: {
  //   background: theme.modal.maskDarken,
  // },
  autoFocusButton: null,
  okButtonProps: {
    className: 'c-btn is--danger',
    style: { minWidth: 80, marginRight: 24 },
  },
  cancelButtonProps: {
    className: 'c-btn is--outline mr-0 float-right',
    style: { minWidth: 80 },
  },
  ...props,
})

export const confirm = ({
  onOk,
  onCancel,
  content = '刪除後將無法還原，確認繼續？',
  ...props
}: ModalFuncProps) => {
  Modal.confirm({
    ...modalDefaultProps(content),
    ...props,
    onOk: () => {
      if (onOk) {
        onOk()
      }
    },
    onCancel() {
      if (onCancel) {
        onCancel()
      }
    },
  })
}
