import React from 'react'
import { atom, atomFamily, useRecoilState } from 'recoil'
import {
  find,
  mergeAll,
  map,
  values,
  head,
  keys,
  path,
  pathOr,
  propEq,
  sort,
  last as Rlast,
  omit,
  inc,
  filter,
} from 'ramda'
import moment from 'moment'
import fakeSetting from '@/mocks/fake-snippet-setting.json'
import { reportTemplateById } from '@/components/report-template-list'
import { getNumberFromString } from '@/utils/webHelper'
import { confirm } from '@/utils/message'
const { compose } = require('ramda')
const faker = require('faker')

type SnippetList = { [key: string]: any[] }

const reportTemplateCategory = {
  CLINICAL_HISTORY: 'Clinical History',
  HIGHT_RISK_REASONS: 'Hight Risk Reasons',
  DATA_OF_PRIOR_SIMILAR_STUDY: 'Data of prior similar study',
  IMAGING_PROTOCOL: 'Imaging Protocal',
  TUMOR_LOCATION: 'Tumor Location/Size',
}

const fakeSnippetSetting = compose(
  mergeAll,
  map((key: string) => ({
    [key]: compose(
      map((x: any) => ({
        ...x,
        Component: path([x.type, 'Component'])(reportTemplateById),
      })),
      pathOr([], [key])
    )(fakeSetting),
  })),
  keys
)(fakeSetting) as { [key: string]: any[] }

const fakeReportTemplateList = Array.from({ length: 100 }).map((_, idx) => ({
  id: idx + 1,
  name: faker.lorem.word(),
  author: faker.name.findName(),
  status: 1,
  statusName: '已發佈',
  createdAt: moment(),
  createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
  updatedAt: moment(),
  updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
  description: faker.lorem.word(),
  setting: fakeSnippetSetting,
}))

const defaultSnippetList = compose(
  mergeAll,
  map((key: string) => ({ [key]: [] })),
  values
)(reportTemplateCategory)

const reportSnippetsState = atomFamily<SnippetList, React.Key | undefined>({
  key: 'reportSnippetsState',
  default: id =>
    compose(
      pathOr(defaultSnippetList, ['setting']),
      find(propEq('id', Number(id)))
    )(fakeReportTemplateList),
})

const snippetCategoryState = atom<string>({
  key: 'snippetCategoryState',
  default: compose(head, values)(reportTemplateCategory),
})

const reportTemplateListState = atom({
  key: 'reportTemplateListState',
  default: {
    content: fakeReportTemplateList,
    total: 100,
  },
})

const reportPreviewState = atom({
  key: 'reportPreviewState',
  default: false,
})

// 模版報告資料
export function useReportSnippets(id?: React.Key) {
  const [_snippetList] = useRecoilState(reportSnippetsState(id))

  React.useEffect(() => {
    setSnippetList(_snippetList)
  }, [JSON.stringify(_snippetList)]) // eslint-disable-line

  const [snippetList, setSnippetList] = React.useState<SnippetList>(
    _snippetList
  )

  const [category, setCategory] = useSnippetCategory()

  const handleTabUpdate = (data: any[]) => {
    setSnippetList(prev => ({ ...prev, [category]: data }))
  }

  const handleTabChange = (activeKey: string) => {
    setCategory(activeKey)
  }

  const handleTabMove = (order: string[]) => {
    const snippetKeys = keys(snippetList)

    setSnippetList(
      compose(
        mergeAll,
        map((key: string) => ({ [key]: snippetList[key] })),
        sort((a: string, b: string) => {
          const orderA = order.indexOf(a)
          const orderB = order.indexOf(b)

          if (orderA !== -1 && orderB !== -1) {
            return orderA - orderB
          }
          if (orderA !== -1) {
            return -1
          }
          if (orderB !== -1) {
            return 1
          }

          const ia = snippetKeys.indexOf(a)
          const ib = snippetKeys.indexOf(b)

          return ia - ib
        }),
        keys
      )(snippetList)
    )
  }

  const handleTabAdd = () => {
    const last = compose(
      inc,
      pathOr(0, []),
      getNumberFromString,
      Rlast,
      sort((a: any, b: any) => a - b),
      keys
    )(snippetList)

    setSnippetList(prev => ({ ...prev, [`新標籤頁${last}`]: [] }))
    setCategory(`新標籤頁${last}`)
  }

  const handleTabRemove = (targetKey: string) => {
    confirm({
      title: '確認要刪除此頁籤嗎？',
      content: '刪除會連同模版資料一起刪除，確認繼續？',
      onOk: () => {
        setSnippetList(prev => omit([targetKey], prev))

        if (category === targetKey) {
          setCategory(
            compose(
              Rlast,
              filter((key: string) => key !== targetKey),
              keys
            )(snippetList)
          )
        }
      },
    })
  }

  const handleTabNameChange = (targetKey: string) => (
    evt: React.FocusEvent<HTMLSpanElement>
  ) => {
    const newTabName = evt.target.innerHTML

    setSnippetList(prev =>
      compose(
        mergeAll,
        map((key: string) => ({
          [key === targetKey ? newTabName : key]: prev[key],
        })),
        keys
      )(prev)
    )
    setCategory(newTabName)
  }

  const handleTabEdit = (
    e:
      | string
      | React.MouseEvent<Element, MouseEvent>
      | React.KeyboardEvent<Element>,
    action: 'add' | 'remove'
  ) => {
    const handler = action === 'add' ? handleTabAdd : handleTabRemove

    handler(e as string)
  }

  return {
    category,
    snippetList,
    setSnippetList,
    handleTabEdit,
    handleTabUpdate,
    handleTabChange,
    handleTabMove,
    handleTabAdd,
    handleTabRemove,
    handleTabNameChange,
  }

  // return snippetList, setSnippetList] as [
  //   SnippetList,
  //   React.Dispatch<React.SetStateAction<SnippetList>>
  // ]
}

export function useSnippetCategory(values?: any) {
  const state = useRecoilState(snippetCategoryState)

  React.useEffect(() => {}, [])

  return state
}

// 報告模版清單
export function useReportTemplateList() {
  return useRecoilState(reportTemplateListState)
}

// 切換編輯/檢視模式
export function useReportPreview() {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

// 更方便切換編輯/檢視模式
export function useToggleReportPreview(state: boolean = false) {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  React.useEffect(() => {
    setIsPreview(state)

    return () => {
      setIsPreview(false)
    }
  }, [state]) // eslint-disable-line

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}
