import React from 'react'
import { Layout } from 'antd'
import cx from 'classnames'
import AppFooter from '@/components/app-footer'
import theme from '../theme'

interface PageProps {
  children: React.ReactNode
  style?: React.CSSProperties
  className?: string
}

export function Page({ children, style, className = 'pr-16' }: PageProps) {
  return (
    <>
      <Layout.Content
        style={{
          background: `linear-gradient(-90deg,#42c2b8,${theme.primary})`,
        }}>
        <div
          className={cx('pl-8 pt-6 pb-12', className)}
          style={style}
          css={{
            background: 'rgb(221, 228, 237)',
            borderTopLeftRadius: 32,
            borderTopRightRadius: 32,
          }}>
          {children}
        </div>
      </Layout.Content>
      <AppFooter />
    </>
  )
}
